
/****Component Home*****/
.container-home {
    min-height: 100vh;
    background-color: #121212; /* Couleur de fond sombre */
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    color: #e0e0e0; /* Couleur du texte claire */
}

/*Home*/
.content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    overflow: hidden;
}


h2 {
    font-size: 50px;
}

p {
    font-size: 22px;
}

button{
    width: fit-content;
    margin: 0 auto;
    padding: 10px 20px;
    font-family: 'Montserrat';
}

.letter-cubes {
    display: flex;
    justify-content: center;
    margin: 20px 0;
}

.cube {
    width: 50px;
    height: 50px;
    border: 2px solid #333; /* Bordure plus sombre */
    margin: 5px;
    font-size: 24px;
    text-align: center;
    line-height: 50px;
    background-color: #1e1e1e; /* Fond des cubes sombre */
    color: #e0e0e0; /* Couleur de texte claire */
}

.correct {
    background-color: #4caf50; /* Vert sombre pour les réponses correctes */
    color: white;
}

.incorrect {
    background-color: #d32f2f; /* Rouge sombre pour les réponses incorrectes */
    color: white;
}

.mobile-input {
    width: 80%;
    padding: 10px;
    font-size: 20px;
    text-align: center;
    margin: 0 auto;
}

.letter-indicator {
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
}

.indicator {
    font-size: 20px;
    padding: 5px;
    margin: 0 2px;
}

.correctMobile {
    background-color: #4caf50; /* Vert sombre pour les réponses correctes */
    color: white;

}

.incorrectMobile {
    background-color: #d32f2f; /* Rouge sombre pour les réponses incorrectes */
    color: white;
}

.number-select{
    width: 100px;
    margin: 0 auto;
    padding: 10px 15px;
}

.interval-div{
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.input-inter{
    margin: 10px 20px;
    padding: 10px 20px;
}